

* { box-sizing: border-box; }

.uom-wrap {
  min-height: 100vh;
  padding: 40px 16px;
  
  display: grid;
  gap: 24px;
  grid-template-columns: 1fr;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: var(--ink);
  background-color: black;
}

@media (min-width: 900px) {
  .uom-wrap {
    grid-template-columns: 1fr 1fr;
    padding: 60px 32px;
  }
}

.uom-card {
  background-color: #111111;
  border-radius: 16px;
  padding: 24px;
  

}

.uom-image {
  width: 160px;
  height: 160px;
  object-fit: cover;
  border-radius: 50%;
  display: block;
  margin: 0 auto 20px;
  border: 4px solid #102d6dff;
}

.uom-name {
  text-align: center;
  margin: 0 0 8px;
  font-size: 1.8rem;
  color: white;
}

.uom-bio {
  text-align: center;
  color: white;
  font-size: 1rem;
  margin-bottom: 20px;
}

.uom-section-title {
  color: white;
  margin: 20px 0 12px;
}

.uom-achievements {
  list-style: disc;
  margin: 0 0 20px 20px;
  color: white;
}

.uom-socials {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.uom-socials a {
  text-decoration: none;
  color: #102d6dff;
  font-weight: bold;
}
.uom-socials a:hover {
  color: var(--pink);
}

.uom-form {
  display: grid;
  gap: 10px;
}
.uom-input, .uom-textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 1rem;
  background: #fff;
}
.uom-textarea {
  resize: vertical;
  min-height: 100px;
}
.uom-btn {
  padding: 10px 20px;
  background: #102d6dff;
  color: #fff;
  font-weight: 700;
  border: none;
  border-radius: 999px;
  cursor: pointer;
  transition: background 0.25s ease;
}
.uom-btn:hover {
  background: var(--pink-dark);
}

.uom-notes {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 12px;
  color: black;
}
.uom-note {
  display: grid;
  grid-template-columns: 48px 1fr;
  gap: 12px;
  padding: 12px;
  border: 1px solid #eee;
  border-radius: 12px;
  background: #fff;
}
.uom-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #102d6dff;
  color: #fff;
  display: grid;
  place-items: center;
  font-weight: 800;
}
